












import Vue, {PropOptions} from "vue";
export default Vue.extend({
  name: "CustomAvatarComp",
  data(){
    return {innerClass: ""};
  },
  props: {
    text: {
      type: String,
      default: "Text",
      note: "Use to set label text of field"
    } as PropOptions,
    exteraInf:{
       type: String,
      
      note: "Use to set label text of field"
    } as PropOptions,
    id: { 
      type: Number,
      note:'' 
    } as PropOptions,  
    src:{
        type: String,
      note:'' 
    } as PropOptions,
    customClass: {
      type: Object,          
      note: "Use to set class font size of field"
    } as PropOptions
  },
  mounted(){
    const self = this as unknown as {classes: Record<string, any> | undefined} // classes diye bişi var mı yok mu bilinmiyor. aşağıdaki satır komple anlamsız olabilir.
    
    if (self.classes){
      this.innerClass = Object.keys(self.classes).join(" ");
    }
    this.$emit('mounted');
  },
  destroyed() {
      this.$el?.remove();
   }
 
 
});
